  min-height: 770px;
  width: 600px;
  margin-bottom: 100px;

  &::before {
    content: "";
    background-image: url('../../assets/matrix-bg.png');
    background-size: 100%;
    position: absolute;
    width: 580px;
    height: 500px;
    top: 40px;
    right: 0px;
    bottom: 0px;
    left: 13px;
    overflow: hidden;

    @media screen and (max-width: 800px) {
      width: 340px;
      height: 340px;
      top: 17px;
      right: 0px;
      bottom: 0px;
      left: 19px;
