  .matrix-appointment {
    display: flex;
    flex-direction: column;
    position: absolute;
    bottom: 0;
    width: 100%;

    h3 {
      color: #3a3a3a;
    }

    .life-map_wrapper {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      gap: 5px;


      .life-map_items {
        display: flex;
        flex-direction: column;
        gap: 5px;
        .life-map_item {
          display: flex;
          flex-direction: row;
          align-items: center;
          gap: 5px;
          width: 175px;
          margin-top: 5px;

          .life-map_item-circle {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 25px;
            height: 25px;
            border: 2px solid $matrix-color;
            border-radius: 50%;
            font-weight: bold;
            font-size: 12px;
            color: $matrix-color;
          }

          .circle-bg {
            border: none;
            background-color: $matrix-color;
            color: white;
          }

          .circle-bg-dark {
            border: none;
            background-color: $matrix-color;
            color: white;
          }

          .life-map_item-title {
            font-size: 10px;
            color: $matrix-color;
          }
        }
      }
    }


