
.matrix-selection-page_wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 55px;
  align-items: center;
  height: 50vh;
  margin: auto;
  &::before {
    content: "";
    background-image: url('../../../assets/main-logo.png');
    background-size: 100%;
    position: absolute;
    width: 370px;
    height: 315px;
    top: 15px;
    right: 0px;
    bottom: 0px;
    left: 73px;
    overflow: hidden;

    @media screen and (max-width: 1630px) {
      background-size: 100%;
      width: 270px;
      height: 230px;
    }

    @media screen and (max-width: 960px) {
      display: none;
    }


    @media screen and (max-width: 800px) {
      display: block;
      width: 240px;
      height: 120px;
      top: 0px;
      right: 0px;
      bottom: 0px;
      left: 30px;
    }
  }

  .matrix-selection-page_wrapper_h1 {
    font-size: 100px;
    font-weight: normal;
    font-family: 'Cormorant', serif;
    color: #004BA8;
  }

  .matrix-selection-page_wrapper_h3 {
    font-size: 25px;
    font-weight: normal;
    font-family: 'Cormorant', serif;
    color: #004BA8;
    margin-bottom: -50px;
  }

  .buttons_wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 100%;

    .buttons_wrapper-btn {
      max-width: 320px;
      height: 60px;
      background: radial-gradient(circle, #004BA8 0%, #00397E 100%);
      box-shadow: 0px 3px 10px rgba(0, 0, 0, .25);

      &:hover {
        background:  #004BA8;
      }
    }
  }

  .social-links_wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 25px;
    width: 400px;
    margin-top: 40px;
  }
}

@media screen and (max-width: 800px) {

  .matrix-selection-page_wrapper {
    gap: 35px;

    .matrix-selection-page_wrapper_h1 {
      font-size: 60px;
    }

    .matrix-selection-page_wrapper_h3 {
      font-size: 15px;
      margin-bottom: -30px;
    }

    .buttons_wrapper {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 15px;

      .buttons_wrapper-btn {
        max-width: 250px;
        font-size: 12px;
        padding: 15px;
      }
    }
  }
}

@media screen and (max-width: 470px) {
  .matrix-selection-page_wrapper {
    gap: 35px;
    width: 100%;

    .matrix-selection-page_wrapper_h1 {
      font-size: 40px;
    }
  }
}