$main-color: #004BA8;
$matrix-color: #004BA8;


* {
  box-sizing: border-box;
}

.matrix_wrapper_personal {
  position: relative;
  height: 750px;
  width: 900px;
  margin-bottom: 100px;
  &::before {
    content: "";
    background-image: url('../../assets/matrix-bg.png');
    background-size: 100%;
    position: absolute;
    width: 750px;
    height: 750px;
    top: 15px;
    right: 0px;
    bottom: 0px;
    left: 73px;
    overflow: hidden;

    @media screen and (max-width: 800px) {
      width: 340px;
      height: 340px;
      top: 17px;
      right: 0px;
      bottom: 0px;
      left: 19px;
    }
  }
}

.bg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  opacity: 1;
}

@media screen and (max-width: 1525px) {
  .matrix_wrapper {
    margin-bottom: 20px;
  }
}

.circle {
  display: flex;
  position: absolute;
  z-index: 999;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  border: 2px solid $matrix-color;
  font-weight: bold;
  background: white;
}

