$matrix-color: #004BA8;

@media screen and (max-width: 1120px) {

  * {
    box-sizing: border-box;
  }

  .matrix-compatibility {
    position: relative;
    height: 420px;
    margin-bottom: 100px;
    border: 1px solid rgba(255, 99, 71, 0);
    overflow-x: hidden;
    margin-left: 0%;
  }

  @media screen and (max-width: 1525px) {
    .matrix_wrapper {
      margin-bottom: 20px;
    }
  }

  .matrix-appointment {
    position: absolute;
    bottom: 0;
    width: 100%;

    h3 {
      color: #3a3a3a;
    }

    .life-map_wrapper {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      gap: 10px;

      .life-map_item {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 10px;
        width: 270px;
        margin-top: 5px;

        .life-map_item-circle {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 30px;
          height: 30px;
          border: 2px solid $matrix-color;
          border-radius: 50%;
          font-weight: bold;
        }

        .circle-bg {
          border: none;
          background-color: $matrix-color;
          color: white;
        }

        .circle-bg-dark {
          border: none;
          background-color: $matrix-color;;
          color: white;
        }

        .life-map_item-title {
          font-size: 13px;
          color: $matrix-color;
        }
      }
    }
  }
}

